@font-face {
    font-family: "Aclonica";
    src: local("Aclonica"),
        url("../../Fonts/Aclonica/Aclonica-Regular.ttf") format("truetype");
}

@font-face {
    font-family: "LavishlyYours";
    src: local("LavishlyYours"),
        url("../../Fonts/Lavishly_Yours/LavishlyYours-Regular.ttf") format("truetype");
}

@font-face {
    font-family: "NatoSan";
    src: local("NatoSan"),
        url("../../Fonts/NatoSan/NotoSansMono-Regular.ttf") format("truetype");
}

.profile-parent{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    width: 800px;
    min-height: fit-content;
    color: white;
}

.profile-details{
    height: fit-content;
}

.profile-details-salutation {
    font-family: "NatoSan";
    font-size: 30px;
}

.profile-details-name {
    font-family: "LavishlyYours";
    font-size: 100px;
    color: #FF7700;
    margin-top: -25px;
}

.profile-details-role {
    font-family: "NatoSan";
    font-size: 25px;
    color: #E04D01;
    margin-top: 10px;
    overflow: hidden;
    height: 40px;
    padding-left: 10%;
}

.role-text{
    display: inline;
    float: left;
    margin: 0;
}

.role-list{
    margin-top: 0;
    padding-left: 110px;
    text-align: left;
    list-style: none;
    animation-name: change;
    animation-duration: 10s;
    animation-iteration-count: infinite;
}

.role-list-item {
    line-height:40px;
    margin:0;
}

.profile-details-short-desc {
    font-family: "NatoSan";
    padding: 10px 10px 25px 10px;
}

.profile-detail-main-image {
    border-radius: 30px;
}

@keyframes change {
    0%, 22%, 100% {transform:translate3d(0,0,0);}
    25%, 47% {transform:translate3d(0,-33.33%,0);}
    50%, 72% {transform:translate3d(0,-66.67%,0);}
    75%, 97% {transform:translate3d(0,-33.33%,0);}
}