#resume-sidebar {
    width: 250px;
    height: 50vh;
    background-color: #080b1a;
}

.resume-body-container {
    width: 600px;
    height: 50vh;
    background-color: #080b1a;
    border-radius: 0px 10px 10px 0px;
}

#resume-sidebar .nav-link {
    font-size: 1.25em;
    border-radius: 0px 50px 50px 0px;
}

#resume-sidebar .nav-link:hover,
#resume-sidebar .nav-link:focus {
    background-color:   #080c42;
    color: orangered !important;
    border-radius: 0px 50px 50px 0px;
}

.resume-icons {
    width: 50px;
    background-color: #080c42;
    border-radius: 10px 0px 0px 10px;
}

.resume-default-container {
    width: 580px;
    height: 430px;
}

.skill-card {
    width: 100%;
}

.skill-card .list-group-item {
    border: 0 !important;
}

#resume-sidebar .active{
    background-color:   #080c42 !important;
    color: orangered !important;
    border-radius: 0px 50px 50px 0px !important;
}