.about-card {
    width: 50%;
    background-color: #080b1a;
    border: 0;
    box-shadow: 10px 10px 5px black;
}

.about-title {
    color: orangered;
}

.about-text {
    text-align: justify;
    color: white;
}

.card:hover {
    transform: scale(1.4);
}

.card {
    transition: transform 0.2s ease;
}

.profile-img {
    height: 100%;
    object-fit: cover;
}