body {
  background-color: #02030f;
}

*::-webkit-scrollbar {
  width: 8px;
  background-color: #f5f5f500;
}

.profile-container {
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: #02030fc0;
  border-radius: 30px;
}

.about-me-container {
  margin-top: 50px;
  margin-bottom: 50px;
}

.resume-container {
  margin-top: 50px;
  margin-bottom: 50px;
}

.award-container {
  margin-top: 50px;
  margin-bottom: 50px;
}

hr.hr-style {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    #f0f0f0,
    #00b9ff,
    #59d941,
    #f0f0f0
  );
  width: 80%;
}

.resume-header {
    margin-bottom: 3.5rem;
    color: orangered
}

.resume-header span {
    background-size:  100% 15%;
    background-repeat: repeat-x;
    background-position: left 0% bottom -8%;
}

.weave {
  background-image: repeating-linear-gradient(
      135deg,
      orange 0px,
      orange 7px,
      transparent 7px,
      transparent 14px
    ),
    repeating-linear-gradient(
      45deg,
      red 0px,
      red 7px,
      transparent 7px,
      transparent 14px
    ),
    repeating-linear-gradient(135deg, #02030f 0px, #02030f 7px, #02030f 7px, #02030f 14px);
}
